import {Routes, RouterModule } from '@angular/router';
import { Main } from './main/main.component';
import { CareerList } from './career/careerList.component';
import { RecentProjectsList } from './recentProjects/recentProjectsList.component';
import { EducationList } from './education/educationList.component';
import { ExperienceList } from './experience/experienceList.component';

export const AppRoutes: Routes = [
    { path: "", component: Main },
    { path: "overview", component: Main },
    { path: "career", component: CareerList },
    { path: "recentprojects", component: RecentProjectsList },
    { path: "education", component: EducationList },
    { path: "experience", component: ExperienceList }
];

