import { Component, OnInit } from '@angular/core';
import { EducationModel } from '../shared/educationModel';
import { EducationDataService } from '../shared/educationDataService';

@Component ({
    selector: 'education',
    templateUrl: 'educationList.component.html',
    styles: [` `]
})
export class EducationList implements OnInit {
    public educations:EducationModel[] = [];
    title = 'Education';

    constructor (private educationData: EducationDataService)
    {
        this.educations = educationData.educations;
    }
    ngOnInit() {
        this.educations = this.educationData.loadEducation();
    }
}

