import { Component, OnInit } from '@angular/core';
import { ExperienceModel } from '../shared/experienceModel';
import { ExperienceDataService } from '../shared/experienceDataService';

@Component ({
    selector: 'experience',
    templateUrl: 'experienceList.component.html',
    styles: [` `]
})
export class ExperienceList implements OnInit {
    public experiences:ExperienceModel[] = [];
    title = 'Experience';

    constructor (private experienceData: ExperienceDataService)
    {
        this.experiences = experienceData.experience;
    }
    ngOnInit() {
        this.experiences = this.experienceData.loadExperience();
    }
}

