
import { Component, OnInit } from "@angular/core";
import { CareerDataService } from "../shared/careerDataService";
import { Router } from '@angular/router';
import { Career } from "../shared/career";

@Component({
    selector: 'career',
    templateUrl: "./careerList.component.html",
    styleUrls: []
})
export class CareerList implements OnInit {
    public careers: Career[] = [];
    title = 'Career';

    constructor(private careerData: CareerDataService) {
        this.careers = careerData.career;
    }

    ngOnInit(): void {
        this.careers = this.careerData.loadCareer();
    }
}
