import { Component, OnInit } from '@angular/core';
//import { Router } from '@angular/router';
import { ProjectsDataService } from '../shared/recentProjectsDataService';
import { RecentProjectsModel } from '../shared/recentProjectsModel';

@Component({
  selector: 'recentprojects',
  templateUrl: 'recentProjectsList.component.html',
  styles: []
})
export class RecentProjectsList implements OnInit {
  public recentProjects: RecentProjectsModel[] = [];
  title = 'Recent Projects';

  constructor(private data: ProjectsDataService) {
    this.recentProjects = data.recentProjects;
  }
  ngOnInit(): void {
    this.recentProjects = this.data.loadRecentProjects();
  }
}
